/* Center the login box */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #a8c0ff, #3f2b96);
}

.login-box {
  background-color: white;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 300px;
}

h2 {
  margin-bottom: 20px;
  font-weight: bold;
}

.input-container {
  margin-bottom: 20px;
}

input {
  width: 100%;
  padding: 10px;
  border-radius: 25px;
  border: 1px solid #ddd;
  outline: none;
  font-size: 16px;
  box-sizing: border-box;
}

.login-button {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 25px;
  background-color: #a28ff1;
  color: white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.login-button:hover {
  background-color: #8c7ae6;
}

.signup-link {
  display: block;
  margin-top: 20px;
  color: #999;
  text-decoration: none;
}

.signup-link:hover {
  text-decoration: underline;
}